import React from 'react'

import {Button ,Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle ,} from '@mui/material';

import { useHomeDialogStyles } from '../../styles/Home/homeDialogstyles';


function HomeDialog({open , handleClose}) {
    const classes = useHomeDialogStyles();
    return (
        <div>
            {/* -- dialog --*/}
            <Dialog   Dialog className={classes.dialog} open={open} onClose={handleClose}>
                <DialogTitle className={classes.dialogTitleContainer}>
                    <div className={classes.dialogIcon}>
                        Icon
                    </div>
                    <div className={classes.dialogTitle}>
                        Title
                    </div>
                 </DialogTitle>
                 <DialogContent className={classes.dialogContent}>
                     <DialogContentText>
                        Message
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                     <Button variant="contained" onClick={handleClose}>
                         Cancel
                    </Button>
                    <Button variant="contained" onClick={handleClose}>
                         ok
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    )
}

export default HomeDialog
