import { createTheme } from "@mui/material";


export const lightTheme = createTheme({
    palette:{
        mode: "light",
        common: {
            black: "#000",
            white: "#fff"
        },
        primary:{
            main: "#44b1ff",
            contrastText: "#fff",
            menueText: "#fff",
            drawerBakground: "#44b1ff",
            logo: "#fff",
            background: "#fff",
            backgroundDark: "#bdbdbd"
        },
        secondary:{
            main: "#44b1ff",
        },
    },
})

export const darkTheme = createTheme({
    palette:{
        mode: 'dark',
        common: {
            black: "#000",
            white: "#fff"
        },
        primary:{
            main: "#44b1ff",
            contrastText: "#fff",
            menueText: "#fff",
            drawerBackground: "#272727",
            logo: "#44b1ff",
            background: "#000",
            backgroundDark: "#121212"
        },
        secondary:{
            main: "#383838",
        }
    }
})