import * as React from 'react';
// --importa components from mui
import {Box, Drawer, Toolbar, CssBaseline, List, Typography, Divider, ListItem, ListItemIcon, ListItemText, Avatar, Switch} from '@mui/material';
import MuiAppBar from '@mui/material/AppBar';
// --import icons from mui icons
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import CloseIcon from '@mui/icons-material/Close';

// -- import styles
import { useNavStyles } from '../styles/Nav/navStyles';
import { styled } from '@mui/material/styles';





// -- Nav component
export default function Nav({darkMode, setDarkMode}) {
  const classes = useNavStyles();
  const [open, setOpen] = React.useState(false);
  const handleDrawerOpen = () => {
    setOpen(true);
  };
  const handleDrawerClose = () => {
    setOpen(false);
  };

  // const [darkMode, setDarkMode] = useState(false);


  return (
    <Box className={classes.mainContainer}>
      <CssBaseline />
      {/* App bar - main Top navigation start*/}
      <AppBar position="fixed" open={open}>
        <Toolbar>
          <Typography variant="h6" noWrap className={classes.logo} component="div">
            TPN logo
          </Typography>
          <Switch checked={darkMode} onChange={() => setDarkMode(!darkMode)} />
          <div className={classes.userContainer}>
            <div className={classes.userInfo}>
              <h5>Name</h5>
              <h6>Role</h6>
            </div>
            <Avatar></Avatar>
          </div>
          <IconButton color="inherit" aria-label="open drawer" edge="end"
            onClick={handleDrawerOpen}
            sx={{ ...(open && { display: 'none' }) }}
            >
            <MenuIcon />
          </IconButton>
        </Toolbar>
      </AppBar>
      {/* App bar - main Top navigation end*/}

      {/* ---- menu Drawer start ----*/}
      <Drawer
        className={classes.drawer}
        sx={{
          width: drawerWidth,
          flexShrink: 0,
          '& .MuiDrawer-paper': {
            width: drawerWidth,
          },
        }}
        variant="persistent"
        anchor="right"
        open={open}
      >
        <DrawerHeader className={classes.drawerHeader}>
          <IconButton onClick={handleDrawerClose}>
            <CloseIcon className={classes.closeIcon}/>
          </IconButton>
        </DrawerHeader>
        <Divider />
        {/* ---- menu Drawer image start ----*/}
            <div className={classes.menuImage}>
              <Avatar className={classes.menuAvatar}></Avatar>
            </div>
        {/* ---- menu Drawer image end ----*/}
        <Divider />
        {/* ---- menu Drawer List start ----*/}
        <List>
            <ListItem className={classes.listItem} button>
                <ListItemIcon>
                  <ChevronLeftIcon color="disabled"/>
                </ListItemIcon >
                <ListItemText>
                    Menu 1
                </ListItemText>
            </ListItem>
            <Divider />
            <ListItem className={classes.listItem} button>
                <ListItemIcon>
                  <ChevronLeftIcon color="disabled"/>
                </ListItemIcon >
                <ListItemText>
                    Menu 2
                </ListItemText>
            </ListItem>
            <Divider />
            <ListItem className={classes.listItem} button>
                <ListItemIcon>
                  <ChevronLeftIcon color="disabled"/>
                </ListItemIcon>
                <ListItemText>
                    Menu 3
                </ListItemText>
            </ListItem>
            <Divider />
        </List>
      {/* ---- menu Drawer List end ----*/}
      </Drawer>
      {/* ---- menu Drawer end ----*/}
    </Box>
  );
}





// -- Nav config

const drawerWidth = 240;

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== 'open',
})(({ theme, open }) => ({
  transition: theme.transitions.create(['margin', 'width'], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  ...(open && {
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginRight: drawerWidth,
  }),
}));

const DrawerHeader = styled('div')(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  padding: theme.spacing(0, 1),
  // necessary for content to be below app bar
  ...theme.mixins.toolbar,
  justifyContent: 'flex-start',
}));