// import MUI's styling
import { makeStyles } from "@mui/styles";

// MUI's styling
export const useHomeStyles = makeStyles((theme)=>({
    homeContainer:{
        marginTop:"90px",
        display: "flex",
        justifyContent: "flex-end",
        width: "100%",
        padding: "0 50px"
    },
    formContainer:{
        display: "flex",
        flexDirection: "column",
        maxWidth: "550px",
        margin: "5px 10px",
        padding: "10px",
        width: "100%",
    },
    input:{
        margin: "10px 0!important",
        width: "100%!important",
    },
    ButtonsContainer:{
        display: "flex",
        flexDirection: "row-reverse",
        marginTop: "1rem",
    },
    okBtn:{
        marginLeft: "10px!important"
    },

    // -- @media
    [theme.breakpoints.down("sm")]:{
        homeContainer:{
            marginTop:"90px",
            padding: "0 "
        },
        dialogTitleContainer:{
            width: '270px',
            display: "flex",
            justifyContent: "space-between",
        },
    }
}))
