// import MUI's styling
import { makeStyles } from '@mui/styles';

// MUI's styling
export const useNavStyles = makeStyles((theme)=>({
    mainContainer:{
      display: "flex"
    },
    logo:{
        flexGrow: 1,
        color: theme.palette.primary.logo,
    },
    userContainer:{
      display: "flex",
    },
    userInfo:{
      textAlign: "right",
      margin: "0 10px",
    },
    drawer:{
        '& .MuiDrawer-paper':{
            background: theme.palette.primary.drawerBakground,
        }
    },
    menuImage:{
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      padding: "25px",
    },
    menuAvatar:{
      width: "130px!important",
      height: "130px!important",
    },
    drawerHeader:{
      display: "flex",
      flexDirection: "row-reverse",
    },
    listItem:{
        textAlign: "right!important",
        color: `${theme.palette.primary.menueText}!important`,
      },
      closeIcon:{
        color: `${theme.palette.primary.menueText}!important`,
    }
  }))