import React from 'react'
// import home styles
import { useHomeStyles } from '../styles/Home/homeStyles'
// import components from mui
import { Button, TextField, Box, Stepper, Step, StepLabel, StepContent, Paper, Typography } from '@mui/material';
// import dialog component
import HomeDialog from '../components/Home/HomeDialog';

// steps label and description
const steps = [
    {
      label: 'input 1',
      description: `Enter the value`,
    },
    {
      label: 'input 2',
      description: `Enter the value`,
    },
    {
      label: 'input 3',
      description: `Enter the value`,
    },
  ];


function Home() {
    // styles config
    const classes = useHomeStyles();
    // dialog config
    const [open, setOpen] = React.useState(false);
    const handleClickOpen = () => {
        setOpen(true);
    };
    const handleClose = () => {
        setOpen(false);
    };
    // steps config
    const [activeStep, setActiveStep] = React.useState(0);
    const handleNext = () => {
      setActiveStep((prevActiveStep) => prevActiveStep + 1);
    };
    const handleBack = () => {
      setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };
    const handleReset = () => {
      setActiveStep(0);
    };

    return (
        <main className={classes.homeContainer}>
            <div className={classes.formContainer}>
                {/* steps */}
                <Box sx={{ Width: "100%" }}>
                    <Stepper activeStep={activeStep} orientation="vertical">
                        {steps.map((step, index) => (
                        <Step key={step.label}>
                            <StepLabel
                                optional={
                                index === 2 ? (
                                    <Typography variant="caption">Last step</Typography>
                                    ) : null
                                }>
                                {/* -- step label - title of input -- */}
                                {step.label}
                            </StepLabel>
                            <StepContent>
                                {/* -- step description -- description of input */}
                                <Typography>{step.description}</Typography>
                                <TextField className={classes.input} color="primary" id="outlined-basic" label={step.label} variant="outlined" />
                                <Box sx={{ mb: 2 }}>
                                    <div>
                                        <Button
                                            variant="contained"
                                            onClick={handleNext}
                                            sx={{ mt: 1, mr: 1 }}
                                        >
                                        {index === steps.length - 1 ? 'Finish' : 'Continue'}
                                        </Button>
                                        <Button
                                        disabled={index === 0}
                                        onClick={handleBack}
                                        sx={{ mt: 1, mr: 1 }}
                                        >
                                        Back
                                        </Button>
                                    </div>
                                </Box>
                            </StepContent>
                        </Step>
                        ))}
                    </Stepper>
                        {activeStep === steps.length && (
                        <Paper square elevation={0} sx={{ p: 3 }}>
                            {/* -- end step message --*/}
                            <Typography>All steps completed</Typography>
                            <Button onClick={handleReset} sx={{ mt: 1, mr: 1 }}>
                                Reset
                            </Button>
                        </Paper>
                    )}
                </Box>
                <div className={classes.ButtonsContainer}>
                    <Button onClick={handleClickOpen} className={classes.okBtn} color="success"  variant="outlined">
                        Ok
                    </Button>
                    <Button onClick={handleClickOpen} className={classes.cancelBtn} color="error"  variant="outlined">
                        Cancel
                    </Button>
                </div>
                <HomeDialog open={open} handleClose={handleClose}/>
            </div>
        </main>
    )
}

export default Home
